import React from "react";
import { Switch, Route } from "react-router-dom";
import Home from "./pages/Home";
import About from "./pages/About";
import Projects from "./pages/Projects";
import Blog from "./pages/Blog";
import Contact from "./pages/Contact";
import Jobs from "./pages/Jobs";

const Main = () => (
  <Switch>
    <Route exact={true} path="/portfolio-create-react-app" component={Home} />
    <Route
      exact={true}
      path="/portfolio-create-react-app/about"
      component={About}
    />
    <Route
      exact={true}
      path="/portfolio-create-react-app/experience"
      component={Jobs}
    />
    <Route
      exact={true}
      path="/portfolio-create-react-app/projects"
      component={Projects}
    />
    <Route
      exact={true}
      path="/portfolio-create-react-app/blog"
      component={Blog}
    />
    <Route
      exact={true}
      path="/portfolio-create-react-app/contact"
      component={Contact}
    />
  </Switch>
);

export default Main;
